<template>
  <div class="row">
      <div class="col s12 m12" v-for="post in blogPosts">
        <div class="card">
          <div class="card-content">
            <span class="post-meta">Posted on {{ formatDate(post.published, 'MMM D, YYYY') }}</span>
            <div v-if="getImage(post.content)" v-bind:style="{
              backgroundImage: 'url(' + getImage(post.content) + ')',
              height: '270px',
              backgroundSize: 'cover',
              backgroundPosition: 'center',
              margin: '25px 0'
            }"></div>
            <div class="card-title"><router-link v-bind:to="'/post/' + post.id">{{ post.title }}</router-link></div>
            <p v-html="excerpt(post.content, 200)"></p>
          </div>
          <div class="card-action">
            <router-link v-bind:to="'/post/' + post.id">Read full story</router-link>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import moment from 'moment'
import { textCutter } from '../utility'

export default {
  name: 'Home',
  data () {
    return {
      blogPosts: []
    }
  },
  created: function () {
    this.$http.get('https://www.googleapis.com/blogger/v3/blogs/8853973475156698199/posts?key=AIzaSyCKwI9IBs_xxxDIsDarwblQOWiuzr0cvg8').then((res) => {
      if (res.ok) {
        this.blogPosts = res.body.items
      }
    })
  },
  methods: {
    excerpt: function (value, n) {
      return textCutter(window.$(value).text(), parseInt(n))
    },
    getImage: function (content) {
      const image = window.$(content).find('img')[0]
      return image ? window.$(image).attr('src') : ''
    },
    formatDate: function (datetime, format) {
      return moment(datetime).format(format)
    }
  },
  filters: {

  }
}
</script>

<style>
  .card-title {
    font-size: 30px !important;
    font-weight: bold !important;
    line-height: 33px !important;
    margin-bottom: 10px !important;
  }

  .card-title a {
    color: #636363;
  }

  .post-meta {
    font-size: 12px;
    color: rgba(0, 0, 0, .44);
  }

  .card-content p {
    margin-top: 17px !important;
    font-size: 16px;
    line-height: 1.7;
  }

  .card-content p img {
    display: block;
    margin: 15px auto !important;
  }
</style>



