<template>
  <div v-if="Object.keys(post).length">
    <h3 class="card-title">{{ post.title }}</h3>
    <span class="post-meta">Posted on {{ formatDate(post.published, 'MMM D, YYYY') }}</span>
    <p v-html="post.content" class="single-article-content"/>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  name: 'Single',
  data () {
    return {
      post: {}
    }
  },
  methods: {
    formatDate: function (datetime, format) {
      return moment(datetime).format(format)
    }
  },
  created: function () {
    document.getElementsByTagName('body')[0].style.background = 'white'
    this.$http.get('https://www.googleapis.com/blogger/v3/blogs/8853973475156698199/posts/' + this.$route.params.id + '?key=AIzaSyCKwI9IBs_xxxDIsDarwblQOWiuzr0cvg8').then((res) => {
      if (res.ok) {
        this.post = res.body
      }
    })
  },
  destroyed: function () {
    document.getElementsByTagName('body')[0].style.background = '#fafafa'
  }
}
</script>

<style scoped>
  .card-title {
    color: #636363;
  }

  p {
    font-weight: 400;
    font-style: normal;
    font-size: 18px;
    line-height: 1.8;
    letter-spacing: -.003em;
  }
</style>

<style>
  .single-article-content img {
    width: 100% !important;
    height: auto !important;
    margin: auto -1em 1.5em !important;
  }
</style>



