import VueRouter from 'vue-router'

import App from './App'
import Home from './components/Home'
import Single from './components/Single'

const routes = [
  {
    path: '/',
    component: App,
    children: [
      {
        path: '/post/:id',
        component: Single
      },
      {
        path: '/',
        component: Home
      }
    ]
  }
]

const router = new VueRouter({
  routes // short for routes: routes
})

export default router



